.buttons {
    display: flex;
    justify-content: space-between;
    width: 80%;
    margin-bottom: 50px;
  }
  
  .button-container {
    display: flex;
    align-items: center;
  }
  
  .horario-wrapper {
    display: flex;
    align-items: center;
    margin-right: auto;
  }
  
  .horario, .reserva {
    background-color: #4285f4;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
    margin: 35px 0 20px;
  }
  
  .reserva {
    margin-left: 25px;
  }
  
  .horarios-container {
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 180px;
    padding: 10px;
    background-color: #f5f5f5;
    border: 1px solid #ccc;
    z-index: 1;
    overflow-y: auto;
  }
  
  .horarios-container button {
    font-size: 14px;
    padding: 8px;
    background-color: white;
    border: none;
    width: 100%;
    text-align: left;
    cursor: pointer;
    margin: 5px;
  }

  .fechar {
    background-color: #a30505;
    width: 23px;
    height: 23px;
    color: #fff;
    border: 1px solid #000;
    border-radius: 10%;
    font-weight: bold;
    margin: 10px;
    display: flex;
    justify-content: center;
    cursor: pointer;
  }

  .fechar:hover {
    border: 1px solid #a30505;
    color: #a30505;
    background-color: #fff;
    transition: 500ms;
  }

  button:disabled {
    background-color: #ccc; 
    cursor: not-allowed;
    scale: 1 !important;
    box-shadow: none !important;
  }

  .horario:hover, .reserva:hover {
    scale: 1.1;
    transition: 500ms;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.685);
  }

  
  
  